export default {
  data() {
    return {
      mobile: '',
      password: '',
      showDel: false,
      ishide: false,
      userInfo: {
        openid: '',
        type: '',
        nickname: '',
        avatarurl: ''
      }
    }
  },
  onLoad(option) {
    if(option) {
      console.log(option.old)
    }
  },
  computed: {
    windowHeight() {
      return this.$store.state.windowHeight
    },
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    onInput(e) {
      let value = e.detail.value
      if (value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    clickAction(type) {
      uni.navigateTo({
        url: 'register?type=' + type,
        "animationType": "zoom-fade-out"
      })
    },
    goLogin() {
      let value = /^1[3456789]\d{9}$/.test(this.mobile)
      if(value) {
        if(!this.password) {
          uni.showToast({
            title: '请输入密码',
            icon: 'none'
          })
          return
        }
        this.$http({
          url: this.$api.login,
          data: {
            mobile: this.mobile,
            password: this.password
          },
          cb: (err, res) => {
            if(!err && res.code === 1) {
              // console.log(res.data.userinfo.token)
              
              let userinfo = {
                mobile: res.data.userinfo.mobile,
                token: res.data.userinfo.token
              }
              this.$store.commit('login', userinfo)
              
              uni.setStorageSync('userinfo', userinfo)
              uni.switchTab({
                url: '../index/index'
              })  
              
              // uni.setStorage({
              //   key: 'userinfo',
              //   data: userinfo,
              //   success: function () {
              //     uni.switchTab({
              //       url: '../index/index'
              //     })  
              //   }
              // })
            } else if(res.code === 0 && res.msg) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            } else {
              uni.showToast({
                title: '登录失败',
                icon: 'none'
              })
            }
          }
        })
      } else {
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
      }
    },
    
    // 点击第三方登录
    loginWay(type) {
      let that = this
      that.userInfo.type = type === 'weixin' ? 'weixin': ( type === 'qq' ? 'qq' : 'sinaweibo' )
      uni.getProvider({
        service: 'oauth',
        success: function(res) {
          if (~res.provider.indexOf(type)) {
            uni.login({
              provider: type,
              success: function(loginRes) {
                console.log('第三方登录获取到的信息', JSON.stringify(loginRes))
                
                if(type === 'sinaweibo') {
                  that.userInfo.openid = loginRes.authResult.uid
                } else {
                  that.userInfo.openid = loginRes.authResult.openid
                }
                
                uni.getUserInfo({
                  provider: type,
                  success: function (infoRes) {
                    console.log('头像', infoRes.userInfo.avatarUrl, '用户名', infoRes.userInfo.nickname);
                    that.userInfo.avatarurl = infoRes.userInfo.avatarUrl ? infoRes.userInfo.avatarUrl: ''
                    that.userInfo.nickname = infoRes.userInfo.nickname? infoRes.userInfo.nickname: ''
                    
                    // 正式登录
                    that.getInfo()
                  }
                })
              }
            })
          }
        },
        fail: function(err){
          uni.showToast({
            title: '授权登录失败',
            icon: 'none'
          })
        }
      })
    },
    // 第三方登录获取绑定是否手机号等信息
    getInfo() {
      console.log('111', JSON.stringify(this.userInfo))
      this.$http({
        url: this.$api.otherlogin,
        method: 'POST',
        data: this.userInfo,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            // 绑定了手机
            if(res.data.userinfo.mobile) {
              let userinfo = {
                mobile: res.data.userinfo.mobile,
                token: res.data.userinfo.token
              }
              let data = JSON.stringify(userinfo)
              // 存储token信息
              this.$store.commit('login', userinfo)
              uni.setStorage({
                key: 'userinfo',
                data: data,
                success: function () {
                  uni.switchTab({
                    url: '../index/index'
                  })
                }
              })
              return
            }
            
            // 没绑定手机
            this.$store.commit('setToken', res.data.userinfo.token)
            
            uni.showToast({
              title: '请绑定您的手机号码',
              icon: 'none'
            })
            uni.navigateTo({
              url: 'bindTel'
            })
            
          } else if (res.code === 0 && res.msg){
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } if(err) {
            uni.showToast({
              title: '获取用户绑定信息失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
